.container {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 120px;
  margin: 0;
  font-size: 0.6rem;
  line-height: 1;
  overflow: hidden;
}

.photo {
  width: 100%;
  border-radius: 4px;
}

.info {
  width: 100%;
  opacity: 0.8;
  padding: 5px;
  position: absolute;
  bottom: 0;
  line-height: 1.2;
  color: #fff;
  background: #000;
  line-height: 1.1;
  letter-spacing: 0.2vh;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(9.7px);
  border: 1px solid rgba(254, 254, 254, 0.26);
}
